<template>
	<div class="home-container">
		<el-row>
			<!-- <el-col :span="5">
				<div class="grid-content bg-purple">
		
				</div>
			</el-col> -->
			<el-col :span="17">
				<div class="grid-content bg-purple-light">
					<div class="mid-warp" ref="template">
						<table width="650px" border="0" cellpadding="0" cellspacing="0" align="center" style="width: 650px; margin: 0px auto;">
							<tbody>
								<tr>
									<td bgcolor="#fff">
										<div class="Mpage">
											<draggable class="drag-Mpage" group="people" :list="mConfig" @change="log" ghostClass="ghost">
												<div class="mConfig-item" v-for="(item,key) in mConfig" :key="key" @click="bingConfig(item,key)">
													<div class="content-block-template">
														<component :is="item.name" ref="getComponentData" :configData="propsObj" :index="key" :styleType="styleType" :num="item.num"></component>
													</div>
													<div class="content-block-overlay" v-if="iStemplate">
														<div class="overlay-background"></div>
														<div :class="{'overlay-edited':activeIndex == key}">
															<div class="overlay-edited-background"></div>
														</div>
														<div class="overlay-actions">
															<div class="edit-overlay clickable"></div>
															<!-- <div class="change-variant prev-variant" title="Previous type" @click.stop="Previous(item,key)"><i class="icon el-icon-arrow-left"></i></div> -->
															<div class="overlay-actions-middle">
																<div class="overlay-actions-middle-wrapper clearfix">
																	<div class="action-handle remove-handle" title="Remove" @click.stop="bindDelete(item,key)"><i class="icon el-icon-delete"></i></div>
																</div>
															</div>
															<!-- <div class="change-variant next-variant" title="Next type" @click.stop="Next(item,key)"><i class="icon el-icon-arrow-right"></i></div> -->
														</div>
													</div>
												</div>
											</draggable>
										</div>
									</td>
								</tr>
							</tbody>
						</table>
					</div>
				</div>
			</el-col>
			<el-col :span="7">
				<div class="grid-content bg-purple-light">
					<div class="r-pop-container">
						<div>
							<div style="padding:10px;text-align:center">模版信息</div>
							<div class="select-box">
								<el-form ref="form" label-width="80px">
									<el-form-item label="站点">
										<el-select v-model="value" filterable placeholder="请选择" @change="selectSite">
											<el-option v-for="item in options" :key="item.name" :label="item.name" :value="item.name">
											</el-option>
										</el-select>
									</el-form-item>
								</el-form>
							</div>
						</div>
						<div>
							<div style="padding:10px;text-align:center">组件库</div>
							<div v-for="(item,index) of Lmenu" :key="index">
								<draggable :list="item.list" class="dragArea list-group" chosenClass="chosen" :options="{group:{name: 'people',pull:'clone', put: false},sort: false}" :clone="cloneDog" @change="log">
									<div class="list-group-item " v-for="(element,index) of item.list" :key='index' @click="addDom(element,index)">
										<span class="icon" :class="element.icon"></span>
										{{element.cname}}
									</div>
								</draggable>
							</div>
						</div>
						<div class="right-box">
							<div v-for="(item,key) in rConfig" :key="key">
								<div class="title-bar">{{item.cname}}</div>
								<component :is="item.configName" :activeIndex="activeIndex" :num="item.num" :index="key"></component>
							</div>
						</div>
						<div class="menu-container">
							<div class="menu-btn">
								<el-button type="primary" @click="saveConfig"><span>保存</span></el-button>
								<el-button type="primary" @click="tempSubmit"><span>生成</span></el-button>
							</div>

						</div>
					</div>
				</div>
			</el-col>
		</el-row>
	</div>
</template>
<script>
import vuedraggable from 'vuedraggable';
import MenuConfig from '../components/BasicComponents';
import RpopConfig from '../components/RpopConfig';
import base from '../components/block/baseTemplate';
import Vue from 'vue';
export default {
	name: 'Home',
	data() {
		return {
			Lmenu: [],
			lconfig: [],
			mConfig: [], // 中间组件渲染
			rConfig: [], // 右侧组件配置
			propsObj: {},
			activeIndex: -99, // 选中的下标,
			styleType: 'justify',
			num: 0,
			iStemplate: true,
			value:''
		};
	},
	components: {
		draggable: vuedraggable,
		...MenuConfig,
		...RpopConfig,
	},
	computed: {
		options() {
			const list = [];
			for (let key in this.siteConfig.api) {
				list.push({ name: key });
			}
			return list;
		},
	},
	created() {
		this.lconfig = this.objToArry(MenuConfig);
		this.arraySort();
		console.log(this.$store.state.admin.siteConfig.siteName);
	},
	methods: {
		selectSite(value){
				this.$store.commit(
					'admin/siteConfig/SELECTSITENAME',
					value
				);
		},
		tempSubmit() {
			this.iStemplate = false;
			setTimeout(() => {
				let copytep = $(this.$refs.template);
				let data = copytep.remove('.content-block-overlay');
				//console.log(data.html());
				//let str = copytep.find(".Mpage").append(data)
				let strData = copytep.html().replace(/data-v-(\w)*=""/g, '');
				console.log(
					`${base.templateHeader + strData + base.templateFooter}`
				);
				this.iStemplate = true;
			}, 500);
		},
		objToArry(data) {
			let obj = Object.keys(data);
			let m = obj.map((key) => data[key]);
			return m;
		},
		arraySort() {
			let temp = [];
			let basic = {
				title: '基础组件',
				list: [],
			};
			this.lconfig.map((item) => {
				if (item.type == '1') {
					basic.list.push(item);
				}
			});
			temp.push(basic);
			this.Lmenu = temp;
		},
		cloneDog(data) {
			return {
				...data,
			};
		},
		log(evt) {
			// 中间拖拽排序
			if (evt.moved) {
				this.$store.commit(
					'admin/mobildConfig/defaultArraySort',
					evt.moved
				);
			}
			// 从左向右拖拽排序
			if (evt.added) {
				let data = evt.added.element;
				let obj = {};
				let timestamp = new Date().getTime() * 1000;
				data.num = timestamp;
				this.activeConfigName = data.name;
				let tempItem = JSON.parse(JSON.stringify(data));
				this.rConfig = [];
				this.rConfig.push(tempItem);
				// 保存组件名称
				this.$store.commit(
					'admin/mobildConfig/SETCONFIGNAME',
					data.name
				);
				this.$store.commit(
					'admin/mobildConfig/defaultArraySort',
					evt.added
				);
			}
		},
		addDom(item, index) {
			let obj = {};
			let timestamp = new Date().getTime() * 1000;
			item.num = `${timestamp}`;
			//this.activeConfigName = item.name
			let tempItem = JSON.parse(JSON.stringify(item));
			this.rConfig = [];
			this.mConfig.push(tempItem);
			this.rConfig.push(tempItem);
			this.activeIndex = this.mConfig.length - 1;
			this.$store.commit('admin/mobildConfig/SETCONFIGNAME', item.name);
			this.$store.commit('admin/mobildConfig/ADDARRAY', {
				num: item.num,
				val: item.data().defaultConfig,
			});
		},
		bingConfig(item, index) {
			this.rConfig = [];
			let tempItem = JSON.parse(JSON.stringify(item));
			this.rConfig.push(tempItem);
			this.activeIndex = index;
		},
		bindDelete(item, index) {
			console.log(item);
			this.mConfig.splice(index, 1);
			this.rConfig.splice(0, 1);
			this.$store.commit('admin/mobildConfig/DELETEARRAY', item);
		},
		bindMove(item, index) {},
		saveConfig() {
			let val = this.$store.state.admin.mobildConfig.defaultArray;
			console.log(JSON.stringify(val), '44444444');
			window.localStorage.setItem('template', JSON.stringify(val));
			this.$message.success('保存成功');
			// diySave(this.pageId, {
			// 	name: this.pageName,
			// 	type: this.pageType,
			// 	value: val,
			// }).then((res) => {
			// 	this.$Message.success('保存成功');
			// });
		},
		// 获取默认配置
		getDefaultConfig() {
			const data = JSON.parse(window.localStorage.getItem('template'));
			let obj = {};
			let tempARR = [];
			let newArr = this.objToArry(data);
			newArr.map((el, index) => {
				this.lconfig.map((item, j) => {
					if (el.name == item.configName) {
						item.num = el.timestamp;
						let tempItem = JSON.parse(JSON.stringify(item));
						tempARR.push(tempItem);
						obj[el.timestamp] = el;
						this.mConfig.push(tempItem);
						// 保存默认组件配置
						this.$store.commit('admin/mobildConfig/ADDARRAY', {
							num: el.timestamp,
							val: el,
						});
					}
				});
			});
			// this.$axios.get('/api/test').then(({ data }) => {
			// 	let obj = {};
			// 	let tempARR = [];
			// 	let newArr = this.objToArry(data);
			// 	newArr.map((el, index) => {
			// 		this.lconfig.map((item, j) => {
			// 			if (el.name == item.configName) {
			// 				item.num = el.timestamp;
			// 				let tempItem = JSON.parse(JSON.stringify(item));
			// 				tempARR.push(tempItem);
			// 				obj[el.timestamp] = el;
			// 				this.mConfig.push(tempItem);
			// 				// 保存默认组件配置
			// 				this.$store.commit('admin/mobildConfig/ADDARRAY', {
			// 					num: el.timestamp,
			// 					val: el,
			// 				});
			// 			}
			// 		});
			// 	});
			// });
		},
		Previous(item, index) {
			console.log(item, this.mConfig, this.rConfig);
		},
		Next(item, index) {
			console.log(item);
			// this.num++;
			// if(this.num>1){
			// 	this.num = 0;
			// }
			// const loop = ['justify','center'];
			// this.styleType = loop[this.num];
		},
	},
	mounted() {
		this.getDefaultConfig();
	},
};
</script>
<style lang="less" scoped>
.mid-warp {
	width: 100%;
	height: 100vh;
	background-color: #f2f2f2;
	overflow-y: scroll;
}
.dragArea.list-group {
	display: flex;
	flex-wrap: wrap;
	align-content: flex-start;
	padding: 10px 30px;

	.list-group-item {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		width: 74px;
		height: 66px;
		border-radius: 10px;
		margin-right: 10px;
		margin-bottom: 10px;
		background: #f7f7f7;
		font-size: 12px;
		color: #666;
		cursor: pointer;
		box-shadow: 2px 2px 5px #000;
	}
	.list-group-item:hover {
		box-shadow: none;
	}
}
.icon {
	font-size: 24px;
}
.mConfig-item {
	position: relative;
}
.content-block-overlay {
	.show {
		display: block;
	}
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	.overlay-background {
		position: absolute;
		width: 100%;
		height: 100%;
		background-color: rgba(102, 102, 102, 0.25);
		display: none;
	}
	&:hover {
		.overlay-background {
			display: block;
		}
		.overlay-actions {
			display: block;
		}
	}
	.overlay-edited {
		position: absolute;
		width: 100%;
		height: 100%;
		border: 2px dashed #4db159;
		display: block;
		box-sizing: border-box;
		.overlay-edited-background {
			background: #fff;
			opacity: 0;
			width: 100%;
			height: 100%;
		}
	}
	.overlay-actions {
		position: absolute;
		text-align: center;
		width: 100%;
		height: 100%;
		display: none;
		.edit-overlay {
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
			cursor: pointer;
		}
		.change-variant {
			position: absolute;
			top: 15px;
			width: 44px;
			height: 52px;
			line-height: 50px;
			text-align: center;
			background-size: contain;
			z-index: 2;
			display: inline-block;
			cursor: pointer;
		}
		.prev-variant {
			background: url(../assets/imgs/overlay-prev-variant.png) no-repeat;
			left: -16px;
			background-size: contain;
		}
		.next-variant {
			background: url(../assets/imgs/overlay-next-variant.png) no-repeat;
			right: -16px;
			background-size: contain;
		}
		.overlay-actions-middle {
			float: right;
			position: relative;
			margin-right: 70px;
			display: inline-block;
			margin-top: -5px;
			.overlay-actions-middle-wrapper {
				width: auto;
				text-align: center;
				.action-handle {
					width: 40px;
					height: 40px;
					background: url(../assets/imgs/overlay-action-handle.png)
						no-repeat;
					line-height: 40px;
					text-align: center;
					display: block;
					float: left;
					margin: 0 2px;
					cursor: pointer;
				}
			}
		}
	}
}
.m-content-container {
	width: 650px;
	height: 20px;
	margin: 0 auto;
}
.r-pop-container {
	width: 100%;
	height: 100vh;
	.select-box {
		padding: 10px 20px;
		border-bottom: 2px solid #ddd;
	}
	.right-box {
		border: 2px solid #ddd;
		border-radius: 2px;
		padding: 0 20px;

		.title-bar {
			height: 38px;
			line-height: 38px;
			text-align: center;
			color: #333;
			border-radius: 4px;
			border-bottom: 1px solid #eee;
		}
	}
	.menu-container {
		position: fixed;
		bottom: 0px;
		text-align: center;
		width: 560px;
		.menu-btn {
			height: 50px;
			position: relative;
			background: #e4e7ed;
			padding-top: 20px;
		}
		&:hover {
			display: block;
		}
	}
}
.chosen {
	width: 50px;
	height: 50px;
	background: red;
}
.ghost {
	background: #04ff00;
	width: 100%;
	border: 2px dashed #4db159;
	box-sizing: border-box;
}
.clearfix::after {
	clear: both;
	display: block;
	content: '';
	height: 0;
}
</style>


